import React, { Component } from "react";
import "./Home.css";
import { Link, Events, animateScroll as scroll } from "react-scroll";

class Home extends Component {
  state = {
    tasks: 0
  };

  componentDidMount() {
    var scene = document.getElementById("scene");
    scene.width = window.innerWidth;
    scene.height = window.innerHeight;
    var sceneCtx = scene.getContext("2d");
    // Animation canvas begins
    var canvas = document.querySelector("canvas");
    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;
    var c = canvas.getContext("2d");

    addEventListener("resize", () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
      scene.width = window.innerWidth;
      scene.height = window.innerHeight;
    });

    // const colorArray = [
    //   "#2185C5",
    //   "#7ECEFD",
    //   "#FFF6EF",
    //   "#FF7F66",
    //   "#e16363",
    //   "#943855",
    //   "#fce2ae",
    //   "#b2fcff",
    //   "#fbc99d"
    // ];
    const colorArray = ["#04c2c9", "#1b242f", "#e31b6d"];
    const code = [
      "dev",
      "test",
      "bugs",
      "clients",
      "prod",
      "deadline",
      "lastdate",
      "git clone",
      "cd -r rm",
      "sudo"
    ];
    let mouse = {
      x: canvas.width / 2,
      y: canvas.height / 2
    };

    addEventListener("mousemove", function(event) {
      mouse.x = event.clientX;
      mouse.y = event.clientY;
    });

    function randomIntFromRange(min, max) {
      return Math.floor(Math.random() * (max - min + 1) + min);
    }

    function randomColors(colors) {
      return colors[Math.floor(Math.random() * colors.length)];
    }

    //users invisible head
    function particle(x, y, radius, color) {
      this.x = x;
      this.y = y;
      this.color = color;
      this.radius = radius;
      this.radians = Math.random() * Math.PI * 2;
      this.velocity = 0.05;
      this.distanceFromCenter = randomIntFromRange(200, 350);
      this.lastMouse = { x: x, y: y };

      this.update = function() {
        const lastPoint = { x: this.x, y: this.y };

        // Move points over time
        this.radians += this.velocity;

        //Drag effect
        this.lastMouse.x += (mouse.x - this.lastMouse.x) * 0.05;
        this.lastMouse.y += (mouse.y - this.lastMouse.y) * 0.05;

        this.x =
          this.lastMouse.x + Math.cos(this.radians) * this.distanceFromCenter;
        this.y =
          this.lastMouse.y + Math.sin(this.radians) * this.distanceFromCenter;
        this.draw(lastPoint);
      };
      this.draw = lastPoint => {
        c.beginPath();
        c.strokeStyle = this.color;
        c.lineWidth = this.radius;
        c.moveTo(lastPoint.x, lastPoint.y);
        c.lineTo(this.x, this.y);
        c.stroke();
        c.closePath();
        c.fill();
      };
    }

    //Variable Declaration
    let particles;
    function init() {
      particles = [];

      for (let i = 0; i < 200; i++) {
        const radius = Math.random() * 3 + 1;
        particles.push(
          new particle(
            canvas.width / 2,
            canvas.height / 2,
            radius,
            randomColors(colorArray)
          )
        );
      }
    }

    function animate() {
      requestAnimationFrame(animate);
      // c.clearRect(0, 0, innerWidth, innerHeight);
      c.fillStyle = "rgba(255,255,255,0.05)";
      c.fillRect(0, 0, innerWidth, innerHeight);

      particles.forEach(particle => {
        particle.update();
      });
    }
    init();
    animate();
  }

  render() {
    return (
      <div id="container">
        <canvas className="canvas" style={{ zIndex: 5 }} id="canvas1" />
        <canvas className="canvas" id="scene" />
        <div className="introdiv">
          <p style={{ paddingTop: "25%", fontSize: "20px" }}>
            Hi, I'm IAN DSOUZA
          </p>
          <p style={{ fontSize: "20px" }}>I'm a Full Stack Developer...</p>

          {/* <p>
            I'm an experienced Full Stack Developer and currently pursuing my
            masters from{" "}
          </p>
          <p>Queen Mary University of London</p>
          <p>
            This is my personal website, providing insights of my professional
            interests, experiences, Feel free to look around.
          </p> */}
          <p style={{ fontSize: "10px", color: "#04c2c9" }}>
            This is my personal website,Feel free to look around.
          </p>

          <div
            style={{ marginTop: 140, marginLeft: 50 }}
            className="custombutton"
            onClick={() => scroll.scrollMore(1200)}
          >
            <p>View My Work</p>
          </div>
        </div>
      </div>
    );
  }
}
export default Home;



