import React, { Component } from "react";
import SkillBar from "react-skillbars";
import { ServerAddr } from "../../NetConf";
import "./About.css";
import { Row, Col, Alert, Modal, Button } from "react-bootstrap";
import Slide from "react-reveal/Slide";

class About extends Component {
  state = {
    collapsed: true,
    SKILLS: [],
    skillcolours: {
      bar: "#00A1A7",
      title: {
        text: "#fff",
        background: "#04C2C9"
      }
    }
  };

  getSkills = () => {
    ServerAddr.get("skill/").then(res => {
      console.log("skills", res);
      if ((res["status"] = 200)) {
        var data = res["data"]["results"];
        var skills = [];
        //convert skills to data requested by the lib.
        data.forEach(element => {
          var obj = {
            type: element["name"],
            level: element["exp"]
          };
          skills.push(obj);
        });
        this.setState({ SKILLS: skills });
      } else {
      }
    });
  };

  componentDidMount() {
    setTimeout(() => {
      this.setState({ collapsed: false });
    }, 5000);
    this.getSkills();
  }

  //   handleClose = () => {
  //     this.setState({ test: 80 });
  //   };

  render() {
    const { SKILLS, skillcolours } = this.state;
    return (
      <div>
        <div className="title" />
        <p className="titletext">About</p>
        <div />
        <Row>
          <Col md={6}>
            <div className="userimgdiv">
              <Slide left delay={200} duration={3000}>
                <div>
                  <img
                    className="userimg"
                    src="https://dummyimage.com/600x400/04c2c9/f2f3ff"
                  />

                  <div className="descriptionwrapper">
                    <div className="descriptiondiv">
                      <h3 style={{ textAlign: "center", color: "#616161" }}>
                        Whose This Guy?
                      </h3>
                      <p className="desctext">
                        I am a passionate computer engineer and full-stack
                        developer with more than two years of experience. Its my
                        passsion to build web and mobile applications. This is
                        my personal website, providing insights of my
                        professional interests, experiences, Feel free to look
                        around.
                      </p>
                    </div>
                  </div>
                </div>
              </Slide>
            </div>
          </Col>
          <Col md={6}>
            <Slide right delay={200} duration={3000}>
              <SkillBar skills={SKILLS} colors={skillcolours} height={22} />
            </Slide>
          </Col>
        </Row>
      </div>
    );
  }
}

export default About;



